@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap);
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline;box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1.2}ol{padding-left:0;list-style:decimal}ul{padding-left:0;list-style:none}table{border-collapse:collapse;border-spacing:0}canvas{margin:0;padding:0}

.slide{position:absolute;top:0;left:0;right:0;bottom:0;display:flex;justify-content:center;align-items:center}.slide img{position:absolute;top:0;left:0;right:0;bottom:0;object-fit:cover}.slide__cap{background-color:rgba(0,0,0,0.2)}.slide__content{position:relative;width:90%;height:90%;border:1px solid white;border-radius:3px;padding:1em;overflow:auto;transition:all 300ms;display:flex;flex-direction:column;justify-content:flex-start}.slide__content::-webkit-scrollbar{width:4px;height:4px}.slide__content::-webkit-scrollbar-thumb{background-color:rgba(255,255,255,0.3)}.slide__content:hover::-webkit-scrollbar-thumb{background-color:white}.slide__content h1{color:white;font-family:Roboto, 'sans-serif';font-weight:700;font-size:1.5em;border-bottom:2px solid white}.slide__content h3{margin-top:0.5em;color:white;font-family:Roboto, 'sans-serif';font-weight:300;font-size:1.2em}.slide__content p{color:white;font-size:0.9em}.slide__content p:nth-of-type(1){margin-top:2em}.slide__content p+p{margin-top:0.5em}.slide__control{margin-top:auto;display:flex;justify-content:space-between;align-items:center}.slide__control svg{opacity:0.6;width:2em;height:2em;cursor:pointer}.slide__control svg:hover{opacity:1}.slide__control svg:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.slide__number{color:white;font-family:Roboto, 'sans-serif';font-weight:300;font-size:1.5em}@media (min-width: 768px) and (max-width: 1024px){.slide__content{width:80%;height:80%}.slide__content h1{font-size:1.8em}.slide__content h3{font-size:1.5em}.slide__content p{font-size:1.1em}.slide__control svg{width:2.5em;height:2.5em}.slide__number{font-size:2em}}@media (min-width: 1025px){.slide__content{width:70%;height:70%}.slide__content h1{font-size:2em}.slide__content h3{font-size:1.6em}.slide__content p{font-size:1.2em}.slide__control svg{width:3em;height:3em}.slide__number{font-size:2.5em}}.preset_1{background-color:rgba(0,191,255,0.6)}.preset_2{background-color:rgba(255,69,0,0.6)}.preset_3{background-color:rgba(30,144,255,0.6)}.preset_4{background-color:rgba(0,128,128,0.6)}.preset_5{background-color:rgba(90,90,90,0.6)}

.slider{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden}.slider__slide_wrapper{position:absolute;top:0;left:0;width:100%;height:100%}

.app{position:fixed;top:0;left:0;right:0;bottom:0}

